
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 20px;
    color: #fff;
    font-family: 'Arial', sans-serif;
    background-image: url('../images/signup.jpeg');
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
    overflow: hidden;
}

.title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
}

.form {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);

}

.input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    transition: border-color 0.3s ease;
}

.input:focus {
    border-color: #007bff;
}

.button {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button:hover {
    background: linear-gradient(to right, #6a11cb, #2575fc);
    transform: translateY(-2px);
}
.button:active {
    transform: translateY(0);
}
.error {
    margin-bottom: 15px;
    font-size: 0.9rem;
    color: #d9534f;
    text-align: center;
}
@media (max-width: 480px) {
    .form {
        padding: 1.5rem;
    }

    .title {
        font-size: 2rem;
    }

    .input, .button {
        font-size: 0.9rem;
    }
}