.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.6)), url('../images/signup.jpeg');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    position: relative;
    isolation: isolate;
}

.container::after {
    content: '';
    position: absolute;
    inset: 0;
    background: radial-gradient(
            circle at center,
            transparent 0%,
            rgba(0, 0, 0, 0.3) 100%
    );
    z-index: -1;
}

.title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 2rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
    color: #ffffff;
    letter-spacing: 1.5px;
}

.form {
    width: 100%;
    max-width: 390px;
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(10px);
    padding: 2rem 1.5rem;
    border-radius: 12px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.input {
    width: 100%;
    padding: 0.7rem;
    margin-bottom: 1.2rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 6px;
    font-size: 0.95rem;
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
}

.input::placeholder {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.9rem;
}

.button {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    font-weight: 600;
    background: linear-gradient(135deg, #4a90e2 0%, #2575fc 100%);
    border-radius: 6px;
    margin-top: 0.5rem;
}

@media (max-width: 480px) {
    .title {
        font-size: 2rem;
    }

    .form {
        width: 90%;
        padding: 1.5rem 1rem;
    }
}
